import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { useTheme } from "@material-ui/core/styles";
import BackgroundImage from "gatsby-background-image";

const addSource = (sources, source) => {
  const hasSource = sources.find((findSource) => {
    return findSource.src === source.src;
  });

  if (!hasSource) {
    sources.push(source);
  }
};

const ResponsiveBackgroundImage = ({ className, image, children, cover }) => {
  if (!image?.localFile?.childImageSharp) return null;

  const theme = useTheme();
  const sources = [];

  if (image?.thumbnails?.mobile) {
    addSource(sources, {
      ...image.thumbnails.mobile.localFile.childImageSharp.fluid,
    });
  }
  if (image?.thumbnails?.tablet) {
    addSource(sources, {
      ...image.thumbnails.tablet.localFile.childImageSharp.fluid,
      media: theme.breakpoints.up("md").replace("@media ", ""),
    });
  }
  if (image?.thumbnails?.desktop) {
    addSource(sources, {
      ...image.thumbnails.desktop.localFile.childImageSharp.fluid,
      media: theme.breakpoints.up("lg").replace("@media ", ""),
    });
  }
  if (image) {
    addSource(sources, {
      ...image.localFile.childImageSharp.fluid,
      media: theme.breakpoints.up("xl").replace("@media ", ""),
    });
  }

  return (
    <BackgroundImage
      preserveStackingContext
      durationFadeIn={0}
      className={className}
      fluid={sources}
    >
      {children}
    </BackgroundImage>
  );
};

const StyledResponsiveBackgroundImage = styled(ResponsiveBackgroundImage)`
  background-size: ${({ cover }) => `${cover ? "cover" : "auto 100%"}`};
  background-color: transparent;
  background-repeat: no-repeat;
  background-position: ${({ position }) => `${position}`};
`;

StyledResponsiveBackgroundImage.defaultProps = {
  image: {},
  position: "center left",
  cover: false,
};

StyledResponsiveBackgroundImage.propTypes = {
  children: PropTypes.oneOfType([
    PropTypes.arrayOf(PropTypes.node),
    PropTypes.node,
  ]).isRequired,
  image: PropTypes.shape({}).isRequired,
  position: PropTypes.string,
};

export default StyledResponsiveBackgroundImage;
