import React from "react";
import { Container, Paper, Grid, Typography } from "@material-ui/core";
import { RichText } from "prismic-reactjs";
import PropTypes from "prop-types";

import {
  ResponsiveBackgroundImage,
  NonStretchedImage,
} from "@landingpage/core/src/components/Image";
import StyledComponent from "./ContentWithQuoteBox.style";

const ContentWithQuoteBox = ({ slice }) => {
  return (
    <StyledComponent>
      <ResponsiveBackgroundImage image={slice.primary.background} cover>
        <Container className="content">
          <Paper className="card">
            <Grid container spacing={6}>
              <Grid item xl={6} md={6} sm={12} xs={12}>
                <Typography variant="h2">
                  {RichText.asText(slice.primary.box_title.raw || [])}
                </Typography>
                <Typography variant="body1" component="div" gutterBottom>
                  {RichText.render(slice.primary.box_text.raw || [])}
                </Typography>
              </Grid>
              <Grid item xl={6} md={6} sm={12} xs={12} className="quoteContent">
                {slice.primary.quote_image?.localFile && (
                  <NonStretchedImage
                    className="image"
                    fluid={
                      slice.primary.quote_image.localFile.childImageSharp.fluid
                    }
                  />
                )}
                <Typography variant="h4">
                  {RichText.asText(slice.primary.quote_text.raw || [])}
                </Typography>
                <Typography variant="body1" component="div" gutterBottom>
                  {RichText.render(slice.primary.quote_name_date.raw || [])}
                </Typography>
              </Grid>
            </Grid>
            <div className="ratingContent">
              {slice.primary.rating?.localFile && (
                <NonStretchedImage
                  className="image"
                  fluid={slice.primary.rating.localFile.childImageSharp.fluid}
                />
              )}
            </div>
          </Paper>
        </Container>
      </ResponsiveBackgroundImage>
    </StyledComponent>
  );
};

ContentWithQuoteBox.defaultProps = {
  slice: {},
};

ContentWithQuoteBox.propTypes = {
  slice: PropTypes.shape({
    slice_type: PropTypes.string.isRequired,

    primary: PropTypes.shape({
      box_title: PropTypes.shape({
        raw: PropTypes.array.isRequired,
      }).isRequired,
      box_text: PropTypes.shape({
        raw: PropTypes.array.isRequired,
      }).isRequired,
      quote_text: PropTypes.shape({
        raw: PropTypes.array.isRequired,
      }).isRequired,
      quote_name_date: PropTypes.shape({
        raw: PropTypes.array.isRequired,
      }).isRequired,
    }).isRequired,
  }).isRequired,
};

export default ContentWithQuoteBox;
