import React, { useMemo, useState } from "react";
import { useForm, Controller } from "react-hook-form";
import { yupResolver } from "@hookform/resolvers";
import { RichText } from "prismic-reactjs";
import {
  Container,
  Grid,
  Typography,
  TextField,
} from "@material-ui/core";

import * as yup from "yup";

import Button from "@landingpage/core/src/components/Button";
import { ResponsiveBackgroundImage } from "@landingpage/core/src/components/Image";

import StyledComponent from "./TestflowForm.style";

  const updateURLParameter = (url, param, paramVal) =>{
    const newAdditionalURL = "";
    const tempArray = url.split("?");
    const baseURL = tempArray[0];
    const additionalURL = tempArray[1];
    const temp = "";
    if (additionalURL) {
        tempArray = additionalURL.split("&");
        for (var i=0; i<tempArray.length; i++){
            if(tempArray[i].split('=')[0] != param){
                newAdditionalURL += temp + tempArray[i];
                temp = "&";
            }
        }
    }
    const rows_txt = temp + "" + param + "=" + paramVal;
    return baseURL + "?" + newAdditionalURL + rows_txt;
}


export default function ActiveCampaignForm({ custom, slice }) {
const [isSuccess, setIsSuccess] = useState(false);
  const schema = useMemo(() => {
    return yup.object().shape({
      email: yup
        .string()
        .email()
        .required(RichText.asText(slice.primary.email_error.raw)),
    });
  }, slice);

  

  const { handleSubmit, errors, control } = useForm({
    resolver: yupResolver(schema),
    defaultValues: {
      email: "",
      fullname: "",
    },
  });

  const onSubmit = (data) => {
    const formData = {
      u: slice.primary.active_campaign_u,
      f: slice.primary.active_campaign_f,
      s: null,
      c: "0",
      m: "0",
      act: "sub",
      v: "2",
      ...data,
    };
    let formDataObject = new FormData();
    for (let key in formData) {
      formDataObject.append(key, formData[key]);
    }

    fetch("https://withlove-int.activehosted.com/proc.php", {
      method: "POST",
      body: formDataObject,
      mode: "no-cors",
    })
      .then(() => {
        setIsSuccess(true);
        window.history.replaceState('', '', updateURLParameter(window.location.href, "type", "success"));
      })
      .catch(() => {});
  };

  return (
    <StyledComponent>
      <ResponsiveBackgroundImage
        image={slice?.primary?.background}
        position="bottom left"
        cover
        className="backgroundImage"
      >
        {!isSuccess && (
          <Container maxWidth="md" disableGutters className="flow">
            <Grid container direction="column">
              <Typography variant="h2">
                {RichText.asText(slice.primary.form_title.raw)}
              </Typography>
              <Typography className="description" component="div" gutterBottom>
                {RichText.render(slice.primary.form_description.raw)}
              </Typography>
              <form
                method="post"
                className="form"
                onSubmit={handleSubmit(onSubmit)}
              >
                <div className="formName">
                  <Controller
                    as={TextField}
                    placeholder={RichText.asText(slice.primary.input_name.raw)}
                    label={RichText.asText(slice.primary.input_name.raw)}
                    name="fullname"
                    variant="filled"
                    margin="normal"
                    control={control}
                  />
                </div>
                <div className="formEmail">
                  <Controller
                    helperText={errors?.email?.message}
                    error={Boolean(errors?.email?.message)}
                    as={TextField}
                    placeholder={RichText.asText(slice.primary.input_email.raw)}
                    name="email"
                    variant="filled"
                    margin="normal"
                    label={RichText.asText(slice.primary.input_email.raw)}
                    control={control}
                    defaultValue=""
                  />
                </div>
                <Button
                  className="button"
                  color="primary"
                  type="submit"
                  margin="normal"
                  href={slice.primary.cta_link.url}
                  target={slice.primary.cta_link.target}
                >
                    {RichText.asText(slice.primary.cta_title.raw)}{" "}
                </Button>
              </form>
            </Grid>
          </Container>
        )}
        {isSuccess && (

          <Container className="notAvailableContainer">
            <Typography className="notAvailable" component="div">
              {RichText.render(slice.primary.not_available_message.raw)}
            </Typography>
          </Container>
        )}
      </ResponsiveBackgroundImage>
    </StyledComponent>
  );
}
